import React, { Component } from 'react';
import {
  Paper,
  Card,
  CardMedia,
  CardHeader,
  Typography,
  Link,
  Grid,
  Button,
  CardActionArea,
  CardContent,
  makeStyles,
  ClickAwayListener,
  CardActions,
} from '@material-ui/core';
import Pdf from '../img/Resume.pdf';
import tokyo60 from '../img/tokyo60.jpg';
import profilePic from '../img/pp.jpg';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import GitHubIcon from '@material-ui/icons/GitHub';
import MenuBookIcon from '@material-ui/icons/MenuBook';

class Profile extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showCard: false,
      width: window.innerWidth,
      height: window.innerHeight,
    };
  }

  componentWillMount() {
    this.setState({ height: window.innerHeight, width: window.innerWidth });
    // Force refresh
    this.render();
  }

  imageCard() {
    return (
      <div
        style={{
          width: '100vw',
          height: '100vh',
          top: '50%',
          left: '50%',
          WebkitTransform: 'transalte(-50%, -50%)',
          transform: 'translate(-50%, -50%)',
          backgroundColor: 'rgba(128, 128, 128, 0.4)',
          zIndex: 999,
          position: 'fixed',
        }}
      >
        <ClickAwayListener onClickAway={() => this.setState({ showCard: false })}>
          <div
            style={{
              zIndex: 1000,
              position: 'fixed',
              top: '50%',
              left: '50%',
              WebkitTransform: 'transalte(-50%, -50%)',
              transform: 'translate(-50%, -50%)',
              width: this.state.width < 500 ? '90%' : '60%',
            }}
          >
            <Card>
              <CardActionArea>
                <CardMedia
                  image={tokyo60}
                  style={{
                    height: 0,
                    paddingTop: '70%',
                    margin: 'auto',
                  }}
                ></CardMedia>
                <CardContent>
                  <Typography gutterBottom variant="h5" component="h2">
                    Tokyo60 keyboard
                  </Typography>
                </CardContent>
              </CardActionArea>
              <CardActions>
                <Button href="https://tokyokeyboard.com/tokyo60/" target="_blank" rel="noopener">
                  Buy now
                </Button>
              </CardActions>
            </Card>
          </div>
        </ClickAwayListener>
      </div>
    );
  }

  pageWidth() {
    if (this.state.width >= 1000) {
      return '50vw';
    } else if (this.state.width < 1000 && this.state.width >= 500) {
      return '70vw';
    }
    return '80vw';
  }

  render() {
    return (
      <div>
        <div
          style={{
            width: this.pageWidth(),
            height: '100%',
            margin: 'auto',
            position: 'relative',
          }}
        >
          {this.state.showCard && this.imageCard()}
          <Grid container direction="column" justify="center" spacing="2">
            <Grid item>
              <Typography variant="h3" align="left">
                Hi. My name is Sherman.
              </Typography>
            </Grid>
            <Grid item>
              <Typography variant="h5" align="left">
                Welcome to my developer profile.
              </Typography>
            </Grid>
            <Grid item container justify="center">
              <Grid item>
                <img src={profilePic} style={{ minWidth: this.pageWidth() }} />
              </Grid>
            </Grid>
            <Grid item container direction="row" alignItems="center" justify="center" spacing={3}>
              <Grid item>
                <Button href="https://www.linkedin.com/in/shrmnl/" target="_blank" rel="noopener">
                  <LinkedInIcon fontSize="large" />
                </Button>
              </Grid>
              <Grid item>
                <Button href="https://github.com/shermanleejm" target="_blank" rel="noopener">
                  <GitHubIcon fontSize="large" />
                </Button>
              </Grid>
              <Grid item>
                <Button href={Pdf} target="_blank" rel="noopener">
                  <MenuBookIcon fontSize="large" />
                </Button>
              </Grid>
            </Grid>
            <Grid item>
              <Typography variant="p">
                I am a penultimate student in Singapore Management University with a deep interest
                in solving real world problems with code.
              </Typography>
            </Grid>
            <Grid item>
              <Typography variant="p">
                In my free time, I like to play Destiny 2, Civilization VI and Cyberpunk 2077.
              </Typography>
            </Grid>
            <Grid item>
              <Typography variant="p">
                I am also an avid fan of mechanical keyboards. I use a{' '}
                <Link
                  color="secondary"
                  onClick={() => this.setState({ showCard: true })}
                  style={{ cursor: 'pointer' }}
                >
                  Tokyo60 with Glorious Pandas lubed with Tribosys 3204 and EnjoyPBT Kanji Keycaps
                </Link>
                .
              </Typography>
            </Grid>
            <Grid item style={{ paddingBottom: '10vw' }}>
              {' '}
            </Grid>
          </Grid>
        </div>
      </div>
    );
  }
}

export default Profile;
