import React, { Component, useState } from 'react';
import {
  AppBar,
  Toolbar,
  IconButton,
  Typography,
  Button,
  Paper,
  ThemeProvider,
  createMuiTheme,
  Grid,
  Switch,
  makeStyles,
  CssBaseline,
} from '@material-ui/core';
import { orange, lightBlue, deepPurple, deepOrange } from '@material-ui/core/colors';
import MenuIcon from '@material-ui/icons/Menu';
import Profile from './components/Profile';
import ContactMe from './components/ContactMe';
import Projects from './components/Projects';

import ApplicationBar from './components/ApplicationBar';

class Homepage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoaded: false,
      displayPage: 'home',
      isDark: false,
    };
  }

  pages = {
    home: <Profile />,
    contactMe: <ContactMe />,
    projects: <Projects />,
  };

  changeDisplayPage = (pageName) => {
    this.setState({
      displayPage: pageName,
    });
  };

  toggleDarkMode = () => {
    this.setState({
      isDark: !this.state.isDark,
    });
  };

  render() {
    var theme = createMuiTheme({
      palette: {
        type: this.state.isDark ? 'dark' : 'light',
        secondary: {
          main: this.state.isDark ? '#ff3232' : '#498af2',
        },
      },
    });

    return (
      <div
        style={{
          backgroundColor: this.state.isDark ? '#303030' : '#fafafa',
          height: '100%',
        }}
      >
        <ThemeProvider theme={theme}>
          <CssBaseline />
          <ApplicationBar
            changeDisplayPage={this.changeDisplayPage}
            currentPage={this.state.displayPage}
            darkModeSwitchState={this.state.isDark}
            toggleDarkMode={this.toggleDarkMode}
          />
          <Toolbar />
          {this.pages[this.state.displayPage]}
        </ThemeProvider>
      </div>
    );
  }
}

export default Homepage;
